import Item from "./Item"
import { v4 as uuidv4 } from 'uuid';

const Title = () =>{
    
    const data = [
        {name:"nutthanon", amount:1000},
        {name:"isac", amount:500},
        {name:"Josave", amount:200},
        {name:"hello", amount:1000},
        {name:"python", amount:5000}
    ]

    return (
        <ul>
            {data.map((item)=>{
                return <Item {...item} key = {uuidv4()}/>
            })}
        </ul>
    )
}


export default Title